	module.exports = {"data":{"markdownRemark":{"html":"<p><code>alt</code> attributes, also known as alt text, are what browsers will display if they fail to load the image. <code>alt</code> attributes are also important for blind or visually impaired users to understand what an image portrays. And search engines also look at alt attributes.</p>\n<p>In short, every image should have an alt attribute!</p>\n<p>You can add an alt attribute right in the img element like this:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code>\t<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>img</span> <span class=\"token attr-name\">src</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>http://www.your-image-source.com/your-image.jpg<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">alt</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>your alt text<span class=\"token punctuation\">\"</span></span> <span class=\"token punctuation\">/></span></span>\n</code></pre>\n      </div>","fields":{"slug":"/certificates/add-a-text-alternative-to-images-for-visually-impaired-accessibility/"},"frontmatter":{"title":"Add A Text Alternative to Images for Visually Impared Accessibility"}}},"pathContext":{"slug":"/certificates/add-a-text-alternative-to-images-for-visually-impaired-accessibility/"}}

